<template>
  <div id="users">
    <el-form :inline="true" :model="formInline" class="demo-form-inline">

      <el-form-item label="">
        <el-input v-model="formInline.name" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="formInline.gender" placeholder="">
          <el-option v-for="item in genderList" :key="item.value" :label="item.key" :value="item.value"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="onSubmit"></el-button>
        <el-button type="warning" icon="el-icon-circle-plus-outline" @click="showEditDialog()"></el-button>
        <el-button type="success" icon="el-icon-upload2" @click="handleImport()"></el-button>
        <el-button type="info" icon="el-icon-download" @click="handleExport()"></el-button>
      </el-form-item>

    </el-form>

    <el-table :data="tableData" style="width: 100%" v-loading="loading2" element-loading-text="" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="name" label="" width="180"></el-table-column>
      <el-table-column prop="age" label="" width="140"></el-table-column>
      <el-table-column prop="gender" :formatter="genderFormat" label="" width="140"></el-table-column>
      <el-table-column prop="describ" label="" ></el-table-column>
      <el-table-column fixed="right" label="" width="280">
        <template scope="scope">
<!--          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>-->
          <el-button type="success" size="small" icon="el-icon-edit"  @click="showEditDialog(scope.$index)"></el-button>
          <el-button type="danger" size="small" icon="el-icon-delete"  @click="removeData(scope.$index)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <div class="block">
      <div class="r_btn">
          <el-button type="danger" size="small" icon="el-icon-delete" @click="batchRemove"></el-button>
      </div>
      <div class="r_page">
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5,10, 20, 30, 40]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
      </div>
    </div>

    <!-- Form -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="ID" :label-width="formLabelWidth" v-show="false">
          <el-input v-model="form.id" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.password" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.age" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-radio v-model="form.gender" :label="0"></el-radio>
          <el-radio v-model="form.gender" :label="1"></el-radio>
          <el-radio v-model="form.gender" :label="-1"></el-radio>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.describ" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="insertOrUpdate"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog :title="upload.title" :visible.sync="upload.open" width="400px" append-to-body>
      <el-upload
          ref="upload"
          :limit="1"
          accept=".xlsx"
          :headers="upload.headers"
          :action="upload.url"
          :name="upload.excelFile"
          :disabled="upload.isUploading"
          :on-progress="handleFileUploadProgress"
          :on-success="handleFileSuccess"
          :auto-upload="false"
          drag
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip text-center" slot="tip">
<!--          <div class="el-upload__tip" slot="tip">-->
<!--            <el-checkbox v-model="upload.updateSupport" /> -->
<!--          </div>-->
          <span>xlsx</span>
<!--          <el-link type="primary" :underline="false" style="font-size:12px;vertical-align: baseline;" @click="importTemplate"></el-link>-->
        </div>
      </el-upload>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitFileForm"> </el-button>
        <el-button @click="upload.open = false"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title=""
               :visible.sync="dialogTableVisible"
               width="100%">
      <el-table
          :data="excelErrorTableData"
          style="width: 100%;margin-bottom: 20px;"
          row-key="id"
          border
          default-expand-all
          :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
        <el-table-column
            prop="errorRow"
            label=":"
            sortable
            width="180">
        </el-table-column>
        <el-table-column
            prop="errorMsgs"
            label=""
            sortable>
        </el-table-column>
      </el-table>
    </el-dialog>

  </div>
</template>
<style type="text/css" scoped>
.block {
  margin-top: 20px;
  width: 100%;
}
.block .r_btn {
  float: left;
  width: 90px;
}
.block .r_page {
  float: right;
}
</style>
<script type="text/javascript">
import {selectsByPage, updatesByIds,inserts,deletesByIds,selectsByIds,excelExport} from '@/api/user/index'
import { getToken } from '@/utils/token'
import {nanoid} from 'nanoid'

export default {
  data() {
    return {
      tableData: [],
      excelErrorTableData:[{
        id: null,
        errorRow: '',
        errorMsgs: '',
        children: []
      }],
      formInline: {
        name: null,
        gender: null,
        // id:''
      },
      currentPage:1,
      total:0,
      pageSize:5,
      genderList:[
        {key:"",value:0},
        {key:"",value:1},
        {key:"",value:-1},
        {key:"",value: null}
      ],
      dialogFormVisible: false,
      dialogTableVisible:false,
      formLabelWidth: '120px',
      form: {
        id:null,
        name: '',
        password:null,
        age: null,
        gender:null,
        describ:''
      },
      loading2: false,
      multipleSelection: [],
      // 
      upload: {
        // 
        open: false,
        // 
        title: "",
        // 
        isUploading: false,
        // 
        // updateSupport: 0,
        excelFile:'excelFile',
        // // 
        headers: { jwtToken:getToken()},
        // 
        url: "/user/excelImport"
      },
    }
  },
  computed: {
  },
  mounted() {
    this.$store.dispatch('showHeader');
    this.loadData();
  },
  methods: {
    //
    loadData() {
      this.loading2 = true;
      const data = {
        current: this.currentPage,
        size: this.pageSize,
        name: this.formInline.name==""?null:this.formInline.name,
        gender: this.formInline.gender
      };
      selectsByPage(data).then(function(res){
        console.log("@user.result",res.data)
        let result=res.data;
        this.tableData = result.data.records;
        this.total = result.data.total;
        this.loading2 = false;
      }.bind(this)).catch(function (res) {
        this.loading2 = false;
        console.log(res.data);
      }.bind(this));
    },
    //
    onSubmit() {
      this.loadData();
    },
    genderFormat(row) {
      if (row.gender === 0) {
        return ''
      } else if(row.gender === 1) {
        return ''
      }else {
        return ''
      }
    },
    //
    handleSizeChange(val) {
      this.pageSize = val;
      this.loadData();
    },
    //
    handleCurrentChange(val) {
      this.currentPage = val;
      this.loadData();
    },
    //
    async showEditDialog(row) {
      if (row == undefined) {//
        this.form.id = null;
        this.form.name = '';
        this.form.age = null;
        this.form.gender = null;
        this.form.describ = '';
        this.dialogFormVisible = true;
      } else {//
        var data = this.tableData[row];
        this.form.id = data.id;
        //
        await selectsByIds({ids: data.id}).then(res => {
          let data=res.data;
          console.log('')
          console.log(data)
          if (data.code === '000000') {//
            // this.$message({
            //   message: data.msg,
            //   type: 'success'
            // });
            this.form.name = data.data[0].name;
            this.form.age = data.data[0].age;
            this.form.gender = data.data[0].gender;
            this.form.describ = data.data[0].describ;
            this.dialogFormVisible = true;
          } else {
            alert("")
            //
            this.form.id = null;
            this.form.name = '';
            this.form.password=null;
            this.form.age = null;
            this.form.gender = null;
            this.form.describ = '';
            this.dialogFormVisible = false;
            //
            this.loadData();
            return;
          }
        }, res => {
          let error=res.data
          //List
          alert(error.message)
          //
          this.form.id = null;
          this.form.name = '';
          this.form.password=null;
          this.form.age = null;
          this.form.gender = null;
          this.form.describ = '';
          this.dialogFormVisible = false;
          //
          this.loadData();
        });
      }
    },
    async insertOrUpdate() {
      if (this.form.name === "" || this.form.name === null) {
        this.$message.error('');
        return;
      }
      // [
      //   {
      //     "id":3,
      //     "age": 23,
      //     "describ": "33",
      //     "gender": 0,
      //     "name": "33",
      //     "password": "123"
      //   },{
      //   "id":4,
      //   "age": 13,
      //   "describ": "44",
      //   "gender": 1,
      //   "name": "44",
      //   "password": "123"
      // }
      // ]
      //
      let userArr = new Array();
      userArr.push(this.form)
      if(this.form.id==null){//
        //
        await inserts(userArr).then(res => {
          let data=res.data;
          console.log('')
          console.log(data)
          if (data.code === '000000') {//
            this.$message({
              message: data.msg,
              type: 'success'
            });
            //
            this.form.id = null;
            this.form.name = '';
            this.form.password=null;
            this.form.age = null;
            this.form.gender = null;
            this.form.describ = '';
            this.dialogFormVisible = false;
            //
            this.loadData();
          } else {
            alert(data.msg)
          }
        }, res => {
          //List
          alert(res.data.message)
        });
      }else{//
        //
        await updatesByIds(userArr).then(res => {
          let data=res.data;
          console.log('')
          console.log(data)
          if (data.code === '000000') {//
            this.$message({
              message: data.msg,
              type: 'success'
            });
            //
            this.form.id = null;
            this.form.name = '';
            this.form.password=null;
            this.form.age = null;
            this.form.gender = null;
            this.form.describ = '';
            this.dialogFormVisible = false;
            //
            this.loadData();
          } else {
            alert(data.msg)
          }
        }, res => {
          //List
          alert(res.data.message)
        });
      }
    },
    removeData(row) {
      var data = this.tableData[row];
      this.$confirm('"'+data.name+'"?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        //
        //

        await deletesByIds({ids:data.id}).then(res => {
          let data=res.data;
          console.log('')
          console.log(data)
          if (data.code === '000000') {//
            this.$message({
              message: data.msg,
              type: 'success'
            });
            //
            this.loadData();
          } else {
            alert(data.msg)
          }
        }, res => {
          //List
          alert(res.data.message)
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    batchRemove() {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        let ids='';
        this.multipleSelection.forEach(row => {
          // console.log(row.id);
          ids+=row.id+','
        });
        ids=ids.substr(0,ids.length-1);
        await deletesByIds({ids:ids}).then(res => {
          let data=res.data;
          console.log('')
          console.log(data)
          if (data.code === '000000') {//
            this.$message({
              message: data.msg,
              type: 'success'
            });
            //
            this.loadData();
          } else {
            alert(data.msg)
          }
        }, res => {
          //List
          alert(res.data.message)
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },

    /**  */
    handleImport() {
      this.upload.title = "";
      this.upload.open = true;
    },
    // 
    handleFileUploadProgress(event, file, fileList) {
      this.upload.isUploading = true;
    },
    handleFileSuccess(res, file, fileList) {
      this.upload.open = false;
      this.upload.isUploading = false;
      this.$refs.upload.clearFiles();
      let data=res.data;
      console.log(data);
      if(data.code=='000000'){//
        this.$message({
          message: data.msg,
          type: 'success'
        });
        //
        this.loadData();
      }else{//
        console.log(data);
        var arr = [];
        console.log(data.unqualifiedRows);
        for(var j = 0;j<data.unqualifiedRows.length; j++){
          let item=data.unqualifiedRows[j];
          let record=new Object();
          record.id=nanoid();
          record.errorRow=''+item.rowOrder;
          record.errorMsgs='';
          let children=[];

          console.log(item);

          for(var i=0;i<item.rowMsgs.length;i++){
            var item2=item.rowMsgs[i];
            let child=new Object();
            child.id=nanoid();
            child.errorRow=item2.msgName;
            child.errorMsgs='';
            for (var i1=0;i1<item2.unqualifiedMsg.length;i1++){
              child.errorMsgs+='['+item2.unqualifiedMsg[i1]+']';
            }
            children.push(child);
          }
          record.children=children;
          arr.push(record);
        }
        this.excelErrorTableData=arr;
        this.dialogTableVisible=true;
      }
      // this.$alert("<div style='overflow: auto;overflow-x: hidden;max-height: 70vh;padding: 10px 20px 0;'>" + res.data.msg + "</div>", "", { dangerouslyUseHTMLString: true });
      // this.getList();
    },
    // 
    submitFileForm() {
      this.$refs.upload.submit();
    },

    /**  */
    handleExport() {
      const data = {
        // current: this.currentPage,
        // size: this.pageSize,
        name: this.formInline.name==""?null:this.formInline.name,
        gender: this.formInline.gender
      };
      excelExport(data).then(res => {//data,
        let data=res.data;
        let blob = new Blob([data],{type: 'application/octet-stream'});
        let fileName = 'filename.xls';
        // 
        let contentDisposition = res.headers['content-disposition'];
        fileName = decodeURI(contentDisposition.split('=')[1]);
        fileName=fileName.substring(7);//UTF-8''...
        if ('download' in document.createElement('a')) {  // IE
          let link = document.createElement('a');
          link.download = fileName;
          link.style.display = 'none';
          link.href = URL.createObjectURL(blob);
          document.body.appendChild(link);
          link.click();
          URL.revokeObjectURL(link.href) ; // URL 
          document.body.removeChild(link);
        } else {  // IE10+
          navigator.msSaveBlob(blob,fileName);
        }
      }, error => {
        //List
        alert(error.message)
      });
    },

  }
}
</script>
