import requests from '@/api/index'
//
export function selectsByPage(data) {//ajax
    // console.log(1111111)
    return requests({
        // url: '/vue-admin-template/test/list', // 
        url: '/user/byPage',
        method: 'post',
        data // *:,params..,req(es6)
    })
}
